
.App-header {
    height: 62px;
    background: #006667;
  }
  .title{
    float:left;
    margin:16px;
    color: #ffffff;
  }
  .buttonsWrapper{
    float: right;
  }
  .logout{
    margin:16px;
    display: inline-block;
  }
  .homeButton{
    display: inline-block;
  }

  .button{
    color: black;
    border: 1px solid #afafaf;
    padding:6px;
    padding-top: 3px;
    background-color: #e6e6e6;
    text-decoration: none;
  }
  
.homeBox{
    width: 21.5%;
    border-radius: 5px;
    margin-top:10%;
    margin-bottom: 20%;
    height: 300px;
}
.homeBoxButton{
    width: 100%;
    height: 50px;
    font-size: 20pt;
    border-radius: 5px;
    margin-bottom: 7px;
}
.home{
    display: inline-flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
}
.mainButton{
    width: 275px;
    height: 50px;
    border: 1px solid lightgray;
    line-height: 50px;
    border-radius: 5px;
    color: #3da1a1;
    display: block;
    text-align: center;
    margin-bottom: 15px;
}
.subHeader{
    padding-top:15px;
    background-color: #006667;
    display: flex;
}
.tab{
    width: 115px;
    margin-right:15px;
    margin-left:15px;
    border: 1px solid #066678;
    background-color: #008080;
    padding: 5px;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    text-align: center;
    cursor: pointer;
    color: white;
}

.tab.selected{
    background-color: #3da1a1;
}
.personInfo{
    max-width: 265px;
    width: 265px;
    background-color: #008080;
    border-radius: 7px;
    color: snow;
    padding:15px;
    height:300px;
}
.personInfo-Title{
    text-align: center;
    background-color: #006667;
    border-top-right-radius: 7px;
    border-top-left-radius: 7px;
    margin-top:-15px;
    margin-left:-15px;
    margin-right:-15px;
    padding-top:10px;
    padding-bottom:10px;

}
.personInfo-Name{
    text-align: center;
    margin-top:10px;
    margin-bottom: 20px;
}

.personInfo-Age{
    margin-left: 50px;
}

.personAverageInfo-Link{
    margin-top: 155px;
    text-decoration: underline;
    cursor: pointer;
    text-align: center;
}

.personInfo-Link{
    margin-top:90px;
    text-decoration: underline;
    cursor: pointer;
    text-align: center;
}
.lifeSpans-Wrapper{
    display: flex;
    justify-content: space-evenly;
    margin-top: 100px;
}
* {
	font-family: "Lato", sans-serif;
}

body {
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

